'use client';

import { useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { useRouter } from 'next/navigation';
import { collection, addDoc, serverTimestamp } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import Footer from '@/app/components/Footer';

export default function NuevaMascotaPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [formData, setFormData] = useState({
    nombre: '',
    edad: '',
    especie: 'perro',
    tamaño: 'mediano',
    sexo: 'macho',
    castrado: false,
    problemas: '',
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({ ...prev, [name]: type === 'checkbox' ? checked : value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    if (!user) {
      setError('Debes estar autenticado para agregar una mascota.');
      return;
    }
    setLoading(true);
    try {
      await addDoc(collection(db, 'mascotas'), {
        ...formData,
        dueño_uid: user.uid,
        estado: 'con_familia',
        fecha_creacion: serverTimestamp(),
      });
      router.push('/mascotas');
    } catch (err) {
      console.error("Error al agregar mascota: ", err);
      setError('No se pudo agregar la mascota. Inténtalo de nuevo.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold text-center mb-8">Agregar Nueva Mascota</h1>
      <form onSubmit={handleSubmit} className="max-w-lg mx-auto bg-white p-8 rounded-lg shadow-md">
        {error && <p className="text-red-500 text-center mb-4">{error}</p>}

        <div className="mb-4">
          <label htmlFor="nombre" className="block text-gray-700 font-bold mb-2">Nombre</label>
          <input type="text" id="nombre" name="nombre" value={formData.nombre} onChange={handleChange} className="w-full px-3 py-2 border rounded-lg" required />
        </div>

        <div className="mb-4">
          <label htmlFor="edad" className="block text-gray-700 font-bold mb-2">Edad (años)</label>
          <input type="number" id="edad" name="edad" value={formData.edad} onChange={handleChange} className="w-full px-3 py-2 border rounded-lg" required />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
          <div>
            <label htmlFor="especie" className="block text-gray-700 font-bold mb-2">Especie</label>
            <select id="especie" name="especie" value={formData.especie} onChange={handleChange} className="w-full px-3 py-2 border rounded-lg">
              <option value="perro">Perro</option>
              <option value="gato">Gato</option>
              <option value="otro">Otro</option>
            </select>
          </div>
          <div>
            <label htmlFor="sexo" className="block text-gray-700 font-bold mb-2">Sexo</label>
            <select id="sexo" name="sexo" value={formData.sexo} onChange={handleChange} className="w-full px-3 py-2 border rounded-lg">
              <option value="macho">Macho</option>
              <option value="hembra">Hembra</option>
            </select>
          </div>
        </div>

        <div className="mb-4">
            <label htmlFor="tamaño" className="block text-gray-700 font-bold mb-2">Tamaño</label>
            <select id="tamaño" name="tamaño" value={formData.tamaño} onChange={handleChange} className="w-full px-3 py-2 border rounded-lg">
              <option value="chico">Chico</option>
              <option value="mediano">Mediano</option>
              <option value="grande">Grande</option>
              <option value="muy_grande">Muy Grande</option>
            </select>
        </div>

        <div className="mb-6">
          <label className="block text-gray-700 font-bold mb-2">Problemas de Salud</label>
          <textarea id="problemas" name="problemas" value={formData.problemas} onChange={handleChange} rows="3" className="w-full px-3 py-2 border rounded-lg" placeholder="Alergias, condiciones crónicas, etc."></textarea>
        </div>

        <div className="mb-6 flex items-center">
          <input type="checkbox" id="castrado" name="castrado" checked={formData.castrado} onChange={handleChange} className="h-4 w-4 text-violet-600 focus:ring-violet-500 border-gray-300 rounded" />
          <label htmlFor="castrado" className="ml-2 block text-sm text-gray-900">¿Está castrado?</label>
        </div>

        <button type="submit" disabled={loading} className="w-full bg-violet-500 hover:bg-violet-700 text-white font-bold py-3 px-4 rounded-lg disabled:bg-violet-300">
          {loading ? 'Guardando...' : 'Guardar Mascota'}
        </button>

      </form>
      <Footer />
    </main>
  );
}
